import { createFeatureSelector, createSelector } from '@ngrx/store';

import { Loan } from '@backbase/loans-collection-ui-ang';

import { CallState } from '../../models/call-state.model';
import { LoansStoreState } from './store.model';
import { storeKey } from './reducer';

const getLoansState = createFeatureSelector<LoansStoreState>(storeKey);

export const selectLoans = createSelector(getLoansState, ({ loans }: LoansStoreState): Loan[] => loans);
export const selectLoansCallState = createSelector(
  getLoansState,
  ({ loansCallState }: LoansStoreState): CallState => loansCallState,
);

export const selectLoan = createSelector(getLoansState, ({ loan }: LoansStoreState): Loan | undefined => loan);
export const selectLoanCallState = createSelector(
  getLoansState,
  ({ loanCallState }: LoansStoreState): CallState => loanCallState,
);

export const selectShowNavigateToOverview = createSelector(
  getLoansState,
  ({ loansTotalCount }: LoansStoreState): boolean => Boolean(loansTotalCount && loansTotalCount > 1),
);

export const selectLoansCurrentPage = createSelector(
  getLoansState,
  ({ loansCurrentPage }: LoansStoreState): number => loansCurrentPage,
);
export const selectLoansTotalCount = createSelector(
  getLoansState,
  ({ loansTotalCount }: LoansStoreState): number | undefined => loansTotalCount,
);
export const selectAliasUpdateCallState = createSelector(
  getLoansState,
  ({ aliasUpdateCallState }: LoansStoreState): CallState => aliasUpdateCallState,
);
